import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { MenuController, AlertController, LoadingController, NavController, ToastController, Platform, IonSlides } from '@ionic/angular';
import { AccessProviders } from '../providers/access-providers';
import { Storage } from '@ionic/storage';
import { NavparamService } from '../navparam.service';
import { DataService } from '../data.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage {

  slideOpts = {
    initialSlide: 0,
    slidesPerView: 1,
    autoplay: true
  };

  prods: any =[];
  datastorage: any;
  name : any;
  uid: any;
  id: any = 0;
  device = true; 
  web = true;

  constructor(
    private router: Router,
    private menuCtrl: MenuController,
    private dataService: DataService,
    private loadingController: LoadingController,
    private toastController: ToastController,
    private alertController: AlertController,
    private accessProviders: AccessProviders,
    public navController: NavController,
    private storage: Storage,
    private platform: Platform,
    private activatedRoute: ActivatedRoute,
    private navParamService: NavparamService
  ) {
    this.device = this.platform.is('mobile');
    this.web =this.platform.is('desktop');
   }

   
  ngOnInit() { 
    this.activatedRoute.params.subscribe((data : any)=>{
      this.id = data.id; 
      console.log(this.id)
     });
  }

  ionViewWillEnter(){

    if(this.id == 1){
      window.location.reload();
    }
    this.storage.get('storage_xxx'). then((res)=>{
      console.log(res);
        this.datastorage = res;
        this.name = this.datastorage.first_name;
        this.uid=this.datastorage.user_id;
    });
  }
 
}
